@keyframes fade {
  0% {
    opacity: 1;
    scale: 1;
  }
  100% {
    opacity: 0;
    scale: 0.2;
  }
}

.animationController {
  width: 100%;
  height: 100vh;
  background-color: white;
  overflow: hidden;
}

.imageAnimate {
  width: 400px;
  height: 600px;
  background-color: black;
  position: absolute;
  transition: all 5s linear;
  animation: fade 0.3s ease-in-out forwards 0.6s;
}
